// 
export class WordList {
    constructor(data_resource, data_word, data_mean, data_type, temp_word, temp_mean, temp_type, status_edit, status_show) {
        // 
        this.data_resource = data_resource; //  string
        this.data_word = data_word; //  string
        this.data_mean = data_mean; //  array
        this.data_type = data_type; //  string

        // 
        this.temp_word = temp_word; //  string
        this.temp_mean = temp_mean; //  string
        this.temp_type = temp_type; //  string

        // 
        this.status_edit = status_edit; //  boolean
        this.status_show = status_show; //  boolean
    }
}