<template>

    <section class="sticky-section">

        <form>
            <h3>1200+7000</h3>

            <!--  -->
            <div class="group-wrap">
                <fieldset class="form-group">
                    <label for="input-search"></label>
                    <input type="text" placeholder="" id="input-search" v-model="search_input">
                </fieldset>
                <button type="button" @click="searchWord()"></button>
            </div>

        </form>

    </section>

</template>

<script>
import { ref } from 'vue'

export default {
    name: 'WordDictionaryHeader',

    emits: ['eventSearchWord'],

    setup(props, context) {

        // 
        let search_input = ref('')

        // 
        function searchWord() {
            context.emit('eventSearchWord', search_input.value);
        }



        return {
            search_input,

            searchWord,
        }
    }
}
</script>