<template>

    <div class="container">
        <div class="row">
            <div class="col-12">

                <div class="dictionary">

                    <!-- Header -->
                    <WordDictionaryHeader 
                        @eventSearchWord="searchWord"
                    />

                    <!-- Body -->
                    <WordDictionaryBody 
                        :data="data"
                        @eventEditWord="editWord"
                        @eventDelWord="delWord"
                    />


                    <!-- Footer -->
                    <WordDictionaryFooter
                        :data="data"
                        @eventAddWord="addWord"
                    />


                </div>

            </div>
        </div>
    </div>
    
</template>

<script>

import { reactive, onMounted, nextTick } from 'vue'; 

// class
import { WordList } from '@/class/word-dictionary';

// csv
import junior1200_1 from '@/assets/data/word-dictionary/junior-1200-1.csv';  // 1200-
import junior1200_2 from '@/assets/data/word-dictionary/junior-1200-2_modify.csv';  // 1200-
import junior1200_3 from '@/assets/data/word-dictionary/junior-1200-3.csv';  // 1200-
import senior7000_1 from '@/assets/data/word-dictionary/senior_7000-1.csv';  // 7000-1
import senior7000_2 from '@/assets/data/word-dictionary/senior_7000-2.csv';  // 7000-2
import senior7000_3 from '@/assets/data/word-dictionary/senior_7000-3.csv';  // 7000-3
import senior7000_4 from '@/assets/data/word-dictionary/senior_7000-4.csv';  // 7000-4
import senior7000_5 from '@/assets/data/word-dictionary/senior_7000-5.csv';  // 7000-5
import senior7000_6 from '@/assets/data/word-dictionary/senior_7000-6.csv';  // 7000-6

// components
import WordDictionaryHeader from './WordDictionaryHeader';
import WordDictionaryFooter from './WordDictionaryFooter';
import WordDictionaryBody from './WordDictionaryBody';


export default {
    name: 'WordDictionary',
    components: {
        WordDictionaryHeader,
        WordDictionaryFooter,
        WordDictionaryBody,
    },

    setup() {
        // 
        const data = reactive({
            word_list: [],    // 
            search_result_count: 0,  // 
        })


        // 

        //  Junior csv 
        async function getJuniorCSVData(csvArray, resource_name) {

            //  csv 
            for ( let j of csvArray) {
                //  [, , ]
                let arr = j[0].split(/([a-zA-Z\s-' ]+)@(\d+)/g);

                //  
                if ( !arr[3] ) return;
                let arr2 = arr[3].split(/[\uff0c]\(([a-zA-Z]+|)\)/g);
                arr2 = arr2.filter( (item)=> item != '' ); // 

                // 
                for ( let i=0 ; i < arr2.length ; i+=2 ) {
                    let means_arr = arr2[i].split(/[\uff0c]/g); // 
                    means_arr = means_arr.filter( (item)=> item != '' ); // 
                    
                    // 
                    let w = new WordList();
                    w.data_resource = resource_name;
                    w.data_word = arr[1];
                    w.data_mean = means_arr;
                    w.data_type = arr2[i+1];
                    w.status_show = false;

                    data.word_list.push(w);
                }
            }
        }

        //  Junior csv 
        async function getSeniorCSVData(csvArray, resource_name) {

            //  csv 
            for ( let j of csvArray) {
            // for ( let j of wordJSON) {
                //  [, , ]
                let arr = j[0].split(/([a-zA-Z\s-' ]+)@\(([a-zA-Z]+)\.(\d+)\)/g);
                arr = arr.filter( (item)=> item != '' ); // 

                if ( arr.length == 0 ) return;
                // 
                let w = new WordList();
                w.data_resource = resource_name;
                w.data_word = arr[0];
                w.data_mean = arr[3] ? arr[3].split(/[,|;]/g) : [];
                w.data_type = arr[1];
                w.status_show = false;

                data.word_list.push(w);
            }
        }


        // 
        function addWord(obj) {
            // 
            data.word_list.push(obj);
            // data.word_list.unshift(obj);


            // 
            searchWord(obj.data_word);
        }

        // 
        function editWord(index, type) {

            switch(type) {
                // 
                case 'edit':
                    // 
                    data.word_list[index].temp_word = data.word_list[index].data_word;
                    data.word_list[index].temp_mean = data.word_list[index].data_mean.join('');
                    data.word_list[index].temp_type = data.word_list[index].data_type;
                
                    // 
                    data.word_list[index].status_edit = true;
                    break;

                // 
                case 'save':
                    // 
                    data.word_list[index].data_word = data.word_list[index].temp_word;
                    data.word_list[index].data_mean = data.word_list[index].temp_mean.split('');
                    data.word_list[index].data_type = data.word_list[index].temp_type;

                    // 
                    data.word_list[index].status_edit = false;
                    break;

                // 
                case 'cancel':
                    // 
                    data.word_list[index].temp_word = data.word_list[index].data_word;
                    data.word_list[index].temp_mean = data.word_list[index].data_mean.join('');
                    data.word_list[index].temp_type = data.word_list[index].data_type;

                    // 
                    data.word_list[index].status_edit = false;
                    break;
            }
        }

        // 
        function delWord(index) {
            let flag = window.confirm(``);
            if ( !flag ) return;
            data.word_list.splice(index, 1);
        }

        // 
        function searchWord(k) {
            let keyword = k.toLowerCase(); // 
            data.search_result_count = 0; // 

            // 
            for ( let item of data.word_list ) {
                
                // 
                if ( keyword.length == 0 ) {
                    item.status_show = false; // 
            
                // 
                } else {
                    
                    //  word & mean
                    let word_flag = item.data_word.toLowerCase().search(keyword) == -1 ? false : true;
                    let mean_flag = item.data_mean.join(';').toLowerCase().search(keyword) == -1 ? false : true;

                    // 
                    if ( word_flag || mean_flag ) {
                        item.status_show = true;
                        data.search_result_count++;
                    
                    // 
                    } else {
                        item.status_show = false;
                    }
                }
            }

        }




        // 
        onMounted(async ()=>{

            //  csv 
            await getJuniorCSVData(junior1200_1, '1200-');
            await getJuniorCSVData(junior1200_2, '1200-');
            await getJuniorCSVData(junior1200_3, '1200-');

            await getSeniorCSVData(senior7000_1, '7000-1');
            await getSeniorCSVData(senior7000_2, '7000-2');
            await getSeniorCSVData(senior7000_3, '7000-3');
            await getSeniorCSVData(senior7000_4, '7000-4');
            await getSeniorCSVData(senior7000_5, '7000-5');
            await getSeniorCSVData(senior7000_6, '7000-6');



            nextTick(() => {
                // do stuff with the DOM

                
            })
        })



        return {
            data,

            searchWord,
            editWord,
            delWord,
            addWord,
        }
    }
}
</script>