<template>

    <section class="body-section">

        <table class="table caption-top table-wrap">
            <caption> ( {{ initData.search_result_count }}  /  {{ initData.word_list.length }} )</caption>
            <thead>
                <tr>
                    <td>#</td>
                    <td></td>
                    <td> ()</td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            </thead>
            <tbody>
                <tr v-if="initData.search_result_count == 0">
                    <td colspan="6"></td>
                </tr>

                <template v-for="(item, index) of initData.word_list" :key="index">
                    <tr v-if="item.status_show">
                        <td>{{ index + 1 }}</td>
                        <td>
                            <template v-if="!item.status_edit">{{ item.data_word }}</template>
                            <input type="text" v-if="item.status_edit" v-model="item.temp_word">
                        </td>

                        <td>
                            <template v-if="!item.status_edit">{{ item.data_mean.join('') }}</template>
                            <input type="text" v-if="item.status_edit" v-model="item.temp_mean">
                        </td>

                        <td>
                            <template v-if="!item.status_edit">{{ item.data_type }}</template>
                            <input type="text" v-if="item.status_edit" v-model="item.temp_type">
                        </td>

                        <td>{{ item.data_resource }}</td>

                        <td>
                            <button v-if="!item.status_edit" @click="editWord(index, 'edit')"></button>
                            <template v-if="item.status_edit">
                                <button @click="editWord(index, 'save')"></button>
                                <button @click="editWord(index, 'cancel')"></button>
                            </template>

                            <button @click="delWord(index)"></button>
                        </td>
                    </tr>
                </template>
            </tbody>
        </table>

    </section>

</template>

<script>
import { toRef } from 'vue'

export default {
    name: 'WordDictionaryBody',
    props: ['data'],
    emits: [
        'eventEditWord',
        'eventDelWord',
    ],

    setup(props, context) {
        //  props 
        let initData = toRef(props, 'data')


        // 
        function editWord(index, type) {
            context.emit('eventEditWord', index, type)
        }

        // 
        function delWord(index) {
            context.emit('eventDelWord', index)
        }



        return {
            initData,

            editWord,
            delWord,
        }
    }
}
</script>