<template>

    <section class="fixed-section">
        <form>

            <!--  -->
            <div class="group-wrap">
                <fieldset class="form-group">
                    <label for="input-word">*</label>
                    <input type="text" placeholder="" id="input-word" v-model="form.word_input">
                </fieldset>

                <fieldset class="form-group">
                    <label for="input-mean">* ()</label>
                    <input type="text" placeholder="" id="input-mean" v-model="form.mean_input">
                </fieldset>

                <fieldset class="form-group">
                    <label for="input-type"></label>
                    <input type="text" placeholder="..." id="input-type" v-model="form.type_input">
                </fieldset>

                <button type="button" @click="addWord()"></button>
            </div>

        </form>
    </section>

</template>
<script>
import { toRef, reactive } from 'vue';

// class
import { WordList } from '@/class/word-dictionary';


export default {
    name: 'WordDictionaryFooter',

    props: ['data'],
    emits: ['eventAddWord'],

    setup(props, context) {
        //  props 
        let initData = toRef(props, 'data')

        // 
        const form = reactive({
            word_input: '',
            mean_input: '',
            type_input: '',
        })

        // 

        // 
        function checkWord(word) {
            
            let el_index = null;

            // 
            let compare_flag = initData.value.word_list.some((item, index) => {
                el_index = index;
                return item.data_word.toLowerCase() === word.toLowerCase();
            });

            // 
            if ( compare_flag ) {
                let add_flag = window.confirm(` #${el_index + 1} `);
                if ( !add_flag ) return;

            // 
            } 
            // else {
                
            // }
        }


        // 
        function addWord() {
            // 
            if ( form.word_input == '' && form.mean_input == '' ) return;

            // 
            checkWord(form.word_input);

            // 
            let w = new WordList();
            w.data_resource = '';
            w.data_word = form.word_input;
            w.data_mean = form.mean_input ? form.mean_input.split('') : [];
            w.data_type = form.type_input;
            w.status_edit = false;
            w.status_show = false;

            context.emit('eventAddWord', w)

            // 
            form.word_input = '';
            form.mean_input = '';
            form.type_input = '';
        }


        return {
            form,

            addWord
        }
    }
}
</script>